.App {
  text-align: center;
  background-image: url(https://previews.123rf.com/images/cienpies/cienpies1211/cienpies121100027/16307612-colorful-sketch-style-books-seamless-pattern-background-vector-illustration-layered-for-easy-manipul.jpg);
  background-repeat: repeat;
 

}
.container{
  margin: 0;
  padding: 0; 
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
